var ctime = parseInt(Date.now());
const total = 10;
let offset = 0;
let qaId = ""
export default {

	data() {
		return {
			loadingType: 0,
			loadingText: {
				contentdown: "",
				contentrefresh: "正在加载...",
				contentnomore: "无更多文章"
			},
			list: [],
			name: ""
		};
	},
	onLoad(options) {
		qaId = options.id
		this.name = options.name
		this.init()
	},
	onUnload() {
		offset = 0;
	},
	methods: {
		init() {
			this.api.helper.get_qaset_list({
				qaId,
				ctime,
				offset,
				total
			}, res => {
				console.log(res)
				if (res.data.length) {
					this.list = this.list.concat(res.data)
					this.loadingType = 0
					if (res.data.length < total) {
						this.loadingType = 2
					}
				} else {
					this.loadingType = 2
				}

			})
		},
		loadMore() {
			if (this.loadingType != 0) {
				return
			}
			this.loadingType = 1
			offset += total
			this.init()
		},
		showOperate() {}
	},
	onReachBottom() {
		this.loadMore()
	}
}
